	module.exports = {"data":{"markdownRemark":{"html":"<p>A custom photo storage application that does exactly what my wife and I want it to.</p>\n<p>I stopped working on new features for Photo Albums in November 2014 when we discovered some new consumer solutions that might fit our needs well enough.\nWe discussed changing the back-end to store photos using Amazon Cloud Drive instead of a custom setup using S3.\nThat would require a significant rewrite, and is not worth it when we could use alternate services that are backed by a real development team.</p>\n<p>After reviewing the photo storage application landscape on our Mac and iPhones, we are unsatisfied.\nWe don't mind paying plenty of money for this valuable service, but no application gives us the right combination of features.</p>\n<p>The main features we want are centered around targeted photo rediscovery (i.e. search).\nThis application will be web-based but support native features where possible to facilitate photo uploading.</p>\n<p>I'm using this opportunity to learn <a href=\"https://emberjs.com/\">Ember</a>.</p>","fields":{"project":{"title":"Photo Albums","subtitle":"Yet another photo storage application, but this one does exactly what we want.","status":"retired","createdAtTimestamp":"2013-12-30T00:48Z","createdAt":"December 2013","deployedUrl":null,"endedAtTimestamp":"2014-11-17T02:52:21.000Z","endedAt":"November 2014","sourceUrl":null}}},"site":{"siteMetadata":{"title":"Adam Stegman"}}},"pathContext":{"slug":"/projects/photo-albums"}}

